.card {
    transition: .05s;
    background-color: black;
    width: fit-content;
    margin: 1%;
    position: relative;
    -webkit-text-stroke: 2px transparent;
    color: transparent;
    border: 4px solid transparent;
}
.card:hover{
    box-shadow: 0px 0px 4px 4px #0ff;
    transition: .5s;
    border: 4px solid blue;
    -webkit-text-stroke: 2px #000000;
    color: rgb(255, 255, 255);
}
