.button {
    transition: .2s;
    position: absolute;
    right: 2px;
    top: 2px;
    background-color: transparent;
    border: transparent solid;
    color: transparent;
}
.button:hover {
    transition: .2s;
    background-color: rgb(222, 222, 222);
    border: ridge;
    color: black;
}

.image {
    position: static;

}

.name {
    position: absolute;
    
    font-size: 2rem;
    color: rgb(255, 255, 255);
    top: -2rem
}

.specie {
    position: absolute;
    font-size: 1.5rem;
    bottom: 2rem;
    left: 2px
}
.specie:hover {
    font-size: 2rem;
    bottom: 1.5rem;
}


.gender {
    position: absolute;
    font-size: 1.5rem;
    bottom: 0rem;
    left: 2px
}
.gender:hover {
    font-size: 2rem;
    bottom: -0.5rem;
}