.container{
    background-color: red;
    display: flex;
    flex-direction: column;
    align-items:center;
    width: 340px;
    position: absolute;
    left: 0;
    right: 0;
    top: 70px;
    bottom: 0;
    overflow: auto;
}
@media only screen and (max-width: 600px){
	/*Big smartphones [426px -> 600px]*/
    .container{
        flex-direction: row;
        align-items:center;
        width: 100%;
        height: 340px;  
    }
    .charheader{
        display: none;
    }

}